<!--  -->
<template>
    <div @click="$emit('close')" class='serach-info'>
      <i @click="changeSearchShow(false)" class="el-icon-error"></i>
      <serachBar v-show="searchVal == ''" title="实时热搜" :showHotTitle='true' :searchData='serachHots' />
       <transition name="el-fade-in">
         <div v-show="searchVal != ''">
          <serachBar title="歌曲" :searchData='serachs.songs' />
          <serachBar title="歌手" :searchData='serachs.artists' />
          <serachBar title="专辑" :searchData='serachs.albums' />
          <serachBar title="歌单" :searchData='serachs.playlists' />
         </div>
       </transition>
    </div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
import serachBar from './serachBar'
export default {
  components: {serachBar},
  data() {
    return {
      order:['歌曲','歌手','专辑','歌单'],
      serachHots:[],
      serachs:{
        albums:[],
        artists:[],
        playlists:[],
        songs:[]
      }
    }
  },
  // 监听属性 类似于data概念
  computed: {
    ...mapGetters([
      'searchVal'
    ])
  },
  // 监控data中的数据变化
  watch: {
    searchVal(val){
      this.getSearchSuggest(val);
    }
  },
  //方法集合
  methods: {
    ...mapMutations([
      'changeSearchShow'
    ]),
    async getSearchHot(){
      try {
        let res = await this.$api.getSearchHot();
        this.serachHots = res.data;
      } catch (error) {
        
      }
    },
    async getSearchSuggest(val){
      try {
        let {result} = await this.$api.getSearchSuggest(val);
        this.serachs = result;
      } catch (error) {
        this.serachs = {
                          albums:[],
                          artists:[],
                          playlists:[],
                          songs:[]
                        };
      }
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getSearchHot();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    
  },
}
</script>
