    .serach-info{
      padding: 15px;
      box-sizing: border-box;
      position: fixed;
      top: 50px;
      left: 0;
      width: 100vw;
      height: calc(100% - 50px);
      overflow-y: scroll;
      color: #333;
      background: rgba(255, 255, 255, 0.96);
      .el-icon-error{
        position: absolute;
        top: 15px;
        right: 15px;
        font-size: 18px;
      }
    }
</style>